<template>
  <div id="incomings">
    <Header>
      <el-breadcrumb separator-class="el-icon-arrow-right" slot="left">
        <el-breadcrumb-item>预算管理</el-breadcrumb-item>
        <el-breadcrumb-item>收入目标</el-breadcrumb-item>
      </el-breadcrumb>
      <div slot="right">
      </div>
    </Header>
    <div class="content">
      <el-row>
        <el-col :span="4">
          <el-date-picker v-model="yearValue" type="year" placeholder="选择年" @change="selectYearData"  format="yyyy 年" value-format="yyyy"></el-date-picker>
        </el-col>
        <el-col :span="8" :offset="1">
          <el-input placeholder="输入收入或部门或姓名查询" v-model="keywords" @change="search" clearable>
            <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <el-table :data="tableData" border style="width: 100%; margin-top: 20px">
        <el-table-column prop="userName" label="姓名" width="180"> </el-table-column>
        <el-table-column prop="deptName" label="所属部门"></el-table-column>
        <el-table-column pr prop="catalog" label="收入来源"></el-table-column>
        <el-table-column pr prop="incomings" label="目标金额"></el-table-column>
      </el-table>
      <el-pagination :current-page="page" :page-size="15" layout="total, prev, pager, next, jumper" :total="count" align="right" @current-change="changePage" class="pagi">
      </el-pagination>
    </div>
  </div>
</template>
<script>
  import Header from '../../components/Header'
  export default {
    name: 'incomings',
    components: {
      Header
    },
    data() {
      return {
        page: 1,
        limit: 5,
        count: 0,
        keywords: '',
        yearValue: new Date(),
        tableData: [
          {
            userName: "张三",
            deptName: '金融部',
            catalog: '销售',
            incomings: '9999',
          }
        ]
      }
    },
    methods:{
      search() {
        this.page = 1;
        this.keywords = this.keywords.trim();
        this.getIncomingsLists()
      },
      selectYearData() {
        this.page = 1;
        this.year = new Date(this.yearValue).getFullYear();
        this.getIncomingsLists()
      },

      getIncomingsLists() {
        this.$http.get(`/ec/api/exchange?page=${this.page}&limit=${this.limit}&year=${this.year}&keywords=${this.keywords}`).then(res =>{
        let resData = res.data;
          if (resData.errcode === 0) {
            this.count = resData.data.count || 0;
            this.tableData = resData.data.rows ||[];
          }
        })
      },
      changePage(page) {
        this.page = page;
      },
      created(){
        this.getIncomingsLists();
      }
    }
  }
</script>
<style>
.pagi {
  margin: 10px;
}
</style>
